<template>
  <div class="header">
    <div class="header-left-border">
      <div>
        <span class="head-title-span">{{title}}</span>
      </div>
      <div class="head-search" @click="onSearch" v-show="flag">
        <i class="icon-search"></i>
        <span class="search-span">搜索</span>
      </div>
      <div>
        <slot name="head-search"></slot>
      </div>
    </div>
    <div>
      <slot name="head-operation"></slot>
    </div>
    <!-- <complexQury></complexQury> -->
  </div>
</template>

<script>
// import complexQury from './ComplexQuery';
// import HeadOperationItem from './HeadOperationItem';
export default {
  name: 'HeaderTitle',
  props: {
    // 标题名称
    titleName: {
      type: String,
      default: ''
    },
    // 判断搜索按钮是否显示
    flag: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      title: ''
    };
  },
  methods: {
    getTitle() {
      if (this.titleName === '') {
        this.title = this.$route.name;
      } else {
        this.title = this.titleName;
      }
    },
    onSearch() {
      this.$emit('searchButtonClick');
    }
  },

  watch: {
    titleName(newVal, oldVal) {
      // this.title = newVal;
      this.getTitle();
    }
  },

  created() {
    this.getTitle();
  },
  components: {
    // complexQury
    // HeadOperationItem
  }
};
</script>

