import { showErrorNotify } from '@/utils/notify';
import { GAS_NAME } from '@/utils/ajaxConfigure';
// import { showWarningMessage } from '@/utils/message';

import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import VXETable from '@/components/common/Table.vue';
import { GetEnergyWarningByType } from '@/api/energyAll.js';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_STATUS_GANTT,
  ENERGY_TYPE
} from '@/utils/constantsFile';
export default {
  name: 'EnergyAlarm',
  components: {
    HeaderTitle,
    VXETable
  },
  data() {
    return {
      GAS_NAME: GAS_NAME,
      EQUIPMENT_STATUS_GANTT: EQUIPMENT_STATUS_GANTT,
      BTN_TEXT: BTN_TEXT,
      maxHeight: document.body.clientHeight - 300,
      tableLoading: false,
      active: 0,
      type: 0,
      tabs: [
        {
          type: 0,
          name: '水表'
        },
        {
          type: 1,
          name: '电表'
        },
        {
          type: 2,
          name: GAS_NAME + '表'
        }
      ],
      unit: ['m³', 'KW·H', 'm³'],
      searchName: '',
      tableDatas: [],
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      dateTime: [],
      pickerOptions: {
        disabledDate: (current) => {
          return current.getTime() > this.moment(`${this.moment(Date.now()).format('YYYY-MM-DD')} 23:59:59`).format('x');
        }
      }
    };
  },
  computed: {
    tableColumns: function() {
      return [
        {
          field: 'deviceName',
          title: '名称',
          align: 'center'

        },
        {
          field: 'nygjUpperLimit',
          title: `每小时用量上限（${this.unit[this.type]}）`,
          align: 'center'

        },
        {
          field: 'nygjThisHourMeter',
          title: `报警值（${this.unit[this.type]}）`,
          align: 'center'
        },
        {
          field: 'nygjTime',
          title: '报警时间',
          align: 'center',
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss']
        }
      ];
    }
  },
  methods: {
    toggle(i, type) {
      this.active = i;
      this.type = type;
      this.onTablePageChanges(1, 10);
    },

    async findList() {
      try {
        let endTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`;
        let startTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`;

        if (this.dateTime[0] !== undefined) {
          startTime = this.dateTime[0];
          endTime = this.dateTime[1];
        }

        this.tableLoading = true;
        const data = await GetEnergyWarningByType({
          pageSize: this.tablePages.pageSize,
          pageNum: this.tablePages.currentPage,
          energyEnum: ENERGY_TYPE[this.type],
          deviceName: this.searchName,
          startTime: new Date(startTime).getTime(),
          endTime: new Date(endTime).getTime()

        }
        );

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.tableDatas = data.rows;
          this.tablePages.total = data.total;
        } else {
          this.tableDatas = [];
          this.tablePages.total = 0;
          // showWarningMessage(data.message);
        }
      } catch (error) {
        this.tableDatas = [];
        this.tablePages.total = 0;
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    search() {
      this.onTablePageChanges(1, this.tablePages.pageSize);
    },
    reset() {
      this.dateTime = [];
      this.searchName = '';
      this.onTablePageChanges(1, 10);
    }
  },
  mounted() {
    this.findList();
  }
};
