<template>
  <el-container>
    <div class="main">
      <el-header>
        <HeaderTitle />
      </el-header>
      <el-main>

          <div id="tabs">
            <ul class='tabUl'>
              <li
                v-for="(tab,index) in tabs"
                @click="toggle(index,tab.type)"
                :class="{active:active==index}"
                :key="index"
              >{{tab.name}}</li>
            </ul>
          </div>
          <div class="searchDiv">
            <div>
              <el-date-picker
                class="dateTime"
                v-model="dateTime"
                type="daterange"
                align="left"
                size="mini"
                value-format="yyyy-MM-dd HH:mm:ss"
                :range-separator="BTN_TEXT.TO"
                :start-placeholder="BTN_TEXT.STARTTIME"
                :end-placeholder="BTN_TEXT.ENDTIME"
                :picker-options="pickerOptions"
                :default-time="['00:00:00', '23:59:59']"
              ></el-date-picker>
            </div>
             <div>
              <span>设备名称</span>
              <el-input
                v-model='searchName'
                placeholder="请输入设备名称"
                size='mini'>
              </el-input>
            </div>
              <el-button
                @click="search"
                size='mini'
                type="primary"
              >{{BTN_TEXT.SEARCH}}</el-button>
              <el-button
                @click="reset"
                size='mini'
              >{{BTN_TEXT.RESET}}</el-button>
          </div>

           <VXETable id="table"
           :height="$options.filters.getTableHeight(240)"
            show-overflow
            highlight-hover-row
            :tableName="''"
            :show-header="true"
            :tableColumns="tableColumns"
            :tableDatas="tableDatas"
            :tablePage="tablePages"
            :tableLoading="tableLoading"
             @onTablePageChange="onTablePageChanges"
          ></VXETable>

      </el-main>
    </div>
  </el-container>
</template>

<script>
import js from '@/assets/js/energy/EnergyAlarm/index.js';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/energy/EnergyAlarm/index.less';
</style>
