// import { postAplicationJSON, getRequest } from '@/utils/request';

// import { postAplicationJSON } from '@/utils/request';

import { postAplicationJSON, getAplicationJSON } from '@/utils/request';

// 能源总览作日对比
export const GetEnergyComparedInfo = (param, jsonData) =>
  postAplicationJSON('energy/getTheEnergyStateOfYesterdayByDeviceType.action?', param, jsonData);

// 能源总览车间/产线/表分布
export const GetEnergyDistributionInfo = (param, jsonData) =>
  postAplicationJSON('energy/getTheDistributionOfLineUseEnergy.action?', param, jsonData);

// 能源7日历史记录
export const GetEnergyHistoryInfo = (param, jsonData) =>
  postAplicationJSON('energy/theEnergyHistoryRecordOfLastSevenDay.action?', param, jsonData);

// 用电分析 - 尖峰谷
export const GetElectricWaveVal = (param) => getAplicationJSON('energy/queryRecentDaysElectricWave.action?', param);

// 能源分析 - 同比环比
export const GetCompare = (param) => getAplicationJSON('energy/compareToPrevious.action?', param);

// 能源负荷统计报表
export const GetEnergyReport = (param, jsonData) =>
  postAplicationJSON('energyReport/getEnergyLoadReport.action?', param, jsonData);

// 查询今日/本月累计告警记录和次数
export const GetWarningRecord = (param, jsonData) =>
  postAplicationJSON('energy/getTheWarningRecordByEnergyByDeviceType.action?', param, jsonData);

// 查询用电峰谷配置
export const GetElectricWave = (param) => getAplicationJSON('energy/queryElectricWave.action?', param);

// 配置用电峰谷

export const SetElectricWave = (param, jsonData) =>
  postAplicationJSON('energy/setElectricWave.action?', param, jsonData);

// 查询能源告警
export const GetEnergyWarningByType = (param) => getAplicationJSON('energy/queryTypeWarnings.action?', param);

// 查询所有电表
export const deviceGetList = (param, jsonData) =>
  postAplicationJSON('device/getList.action?', param, jsonData);
